import react from 'react';
import './style.css';
import MainPage from './MainPage';
import SignUp from './SignUp';
import { Link } from "react-router-dom";

export default class Login extends react.Component
{
    constructor()
    {
        super();
        this.state = {
            isSignUp :false,
            username:"",
            password:"",
            success : false,
            currUname : ""
        }
        this.onchange = this.onchange.bind(this);
        this.login = this.login.bind(this);
        this.logout = this.logout.bind(this);
        this.signupHandler = this.signupHandler.bind(this);
        this.LoginPage = this.LoginPage.bind(this);
    }

    onchange(e)
    {
        this.setState({
            [e.target.name]:e.target.value
        })
    }

    login(e)
    {
        
        const uname = this.state.currUname;
        const pass = this.state.password;

        if(uname.trim()!=="" && pass.trim()!=="")
        {
            fetch('https://sm-quiz-backend-app.herokuapp.com/login',{
                method:"POST",
                mode: "no-cors",
                body: JSON.stringify({username: uname, password: pass}),
                headers:{
                    "Content-type":"application/json",
                },
            }).then((r)=>{
                if(r.ok)
                    return ({succed:true})
                else
                    return r.json();
            }).then((r)=>{
                if(r.succed===true)
                {
                    this.setState({
                        success:true,
                        username:uname,
                        currUname:"",
                        password:""
                    })
                }
                else
                {
                    alert("userName or Password is incorrect");
                }
            })  
        }
        else
        {
            alert("ussename and password must not be empty");
        }
    }

    logout(e)
    {
        this.setState({
            success:false
        })
    }

    signupHandler()
    {
        this.setState({
            isSignUp:true
        })
    }

    render(){
        return (

            this.state.isSignUp ? <SignUp /> : (
            
            this.state.success ? <MainPage username = {this.state.username}
                logout = {this.logout}/> : (
                    this.LoginPage()
            ))
        )
    }


    LoginPage = ()=> {
        return (
            <div class="wrapper fadeInDown">
                    <div id="formContent">
                        <div class="fadeIn first">
                        </div>
                        
                        <input type="text" class="fadeIn second" name="currUname" placeholder="User Name"
                            value = {this.state.currUname} 
                            onChange={this.onchange}
                        />
                        <input type="password" class="fadeIn third" name="password" placeholder="password" 
                            value = {this.state.password}
                            onChange={this.onchange}
                        />
                        <button class="fadeIn fourth loginBtn" value="Login" onClick={this.login}>Login</button>
                        
    
    
                        <div id="formFooter">
                             <Link class="underlineHover" to="/signUp" onClick={this.signupHandler}>SignUp</Link>
                        </div>
    
                    </div>
                </div>
        )
    }



}