import react from "react";
import { Link } from "react-router-dom";
import "./quiz.css";
import data from "./data";
let queData = [];
export default class Quiz extends react.Component {
    
  constructor(props) {
    super(props);
    this.state = {
      time: {},
      seconds: 40,
      isActive: false,
      index: 0,
      Answer: "",
      score: 0,
      showScore: false,
      loading : true,  
    };
    this.timer = 0;
    this.startTimer = this.startTimer.bind(this);
    this.countDown = this.countDown.bind(this);
    this.toggleActive = this.toggleActive.bind(this);
    this.onNextClick = this.onNextClick.bind(this);
  }
  
  onNextClick() {
    if (this.state.index < queData.length - 1) {
        console.log("selected Answer ", this.state.Answer);
        console.log("Coreect Answer ", queData[this.state.index].Answer);

      if (this.state.Answer == queData[this.state.index].Answer) {
        const sc = this.state.score;
        this.setState({
          score: sc + 1,
        });
      }

      const i = this.state.index + 1;

      this.setState({
        index: i,
      });

      let option = document.querySelectorAll("li.option");
      for (let i = 0; i < option.length; i++) {
        for (let i = 0; i < option.length; i++) {
          if (option[i].classList.contains("isactive")) {
            option[i].classList.remove("isactive");
          }
        }
      }

      console.log(this.state.score);
    }
  }

  secondsToTime(secs) {
    let hours = Math.floor(secs / (60 * 60));

    let divisor_for_minutes = secs % (60 * 60);
    let minutes = Math.floor(divisor_for_minutes / 60);

    let divisor_for_seconds = divisor_for_minutes % 60;
    let seconds = Math.ceil(divisor_for_seconds);

    let obj = {
      h: hours,
      m: minutes,
      s: seconds,
    };
    return obj;
  }

  componentDidMount() {
    fetch(`https://sm-quiz-backend-app.herokuapp.com/quiz/${this.props.topic}`,{
      mode: "no-cors"
    })
      .then((res) => {
        if (res.ok) return res.json();
        else return
      })
      .then((r) => {
        const que = r.map((q) => {
          return {
            Qno: q.Qno,
            Question: q.Question,
            Answer: q.Answer,
            op1: q.op1,
            op2: q.op2,
            op3: q.op3,
          };
        });

        console.log(que);
        queData = que;
        let timeLeftVar = this.secondsToTime(this.state.seconds);
        this.setState({
          time: timeLeftVar,
          loading : false,
        });
        
        this.startTimer();

        console.log("component Did Mount", queData);
      }).catch(err => {
          console.log(err)
      });



  }

  startTimer() {
    if (this.timer == 0 && this.state.seconds > 0) {
      this.timer = setInterval(this.countDown, 1000);
    }
  }

  countDown() {
    // Remove one second, set state so a re-render happens.
    let seconds = this.state.seconds - 1;
    this.setState({
      time: this.secondsToTime(seconds),
      seconds: seconds,
    });

    // Check if we're at zero.
    if (seconds == 0) {
      this.setState({
        showScore: true,
      });
      clearInterval(this.timer);
    }
  }
  toggleActive() {
    let option = document.querySelectorAll("li.option");
    for (let i = 0; i < option.length; i++) {
      option[i].onclick = function () {
        for (let i = 0; i < option.length; i++) {
          if (option[i].classList.contains("isactive")) {
            option[i].classList.remove("isactive");
          }
        }
        option[i].classList.add("isactive");
      };
    }
  }

  handleOptionChange(e) {
    this.setState({
      Answer: e.target.value,
    });

    //console.log(this.state.Answer);
  }

  onSubmit(){
      fetch('https://sm-quiz-backend-app.herokuapp.com/userquiz')
      .then();
  }

  render() {
    
    return (
        this.state.loading ? (<h1>Loading</h1>) : (
      <div class="container">
        <div class="row quizMain">
          <div class="card w-70 ">
            {this.state.showScore ? (
              <div>
                <h1>Your Score : {this.state.score}</h1>
              </div>
            ) : (
              <div class="card-body quizCard">
                <h2 class="card-title">Quiz on Airithmetic</h2>
                <p className="quizTimer">
                  {this.state.time.m} : {this.state.time.s}
                </p>
                <h4>
                  Q{queData[this.state.index].Qno}:{" "}
                  {queData[this.state.index].Question}
                </h4>
                <div id="questions">
                  <ul class="option_group" onClick={this.toggleActive}>
                    <li
                      className="option"
                      value={queData[this.state.index].Answer}
                      onClick={(e) => this.handleOptionChange(e)}
                    >
                      {queData[this.state.index].Answer}
                    </li>
                    <li
                      className="option"
                      value={queData[this.state.index].op1}
                      onClick={(e) => this.handleOptionChange(e)}
                    >
                      {queData[this.state.index].op1}
                    </li>
                  </ul>
                  <ul class="option_group">
                    <li
                      class="option"
                      value={queData[this.state.index].op2}
                      onClick={(e) => this.handleOptionChange(e)}
                    >
                      {queData[this.state.index].op2}
                    </li>
                    <li
                      class="option"
                      value={queData[this.state.index].op3}
                      onClick={(e) => this.handleOptionChange(e)}
                    >
                      {queData[this.state.index].op3}
                    </li>
                  </ul>
                </div>
              </div>
            )}
          </div>
          {this.state.showScore ? (
            <Link to="/" className="btn btn-primary">
              Go To Dashboard
            </Link>
          ) : (
            <div className="quizBtn">
              <button
                class="btn btn-primary"
                disabled={this.state.index === 0}
                onClick={() => {
                  if (this.state.index > 0) {
                    const i = this.state.index;
                    this.setState({
                      index: i - 1,
                    });
                  }
                }}
              >
                Prev
              </button>
              <button
                class="btn btn-primary"
                disabled={this.state.index === queData.length - 1}
                onClick={this.onNextClick}
              >
                Next
              </button>
              <button
                class="btn btn-primary"
                disabled={this.state.index < queData.length - 1}
                onClick={() => {
                  this.setState({
                    showScore: true,
                  });
                }}
              >
                Submit
              </button>
            </div>
          )}
        </div>
      </div> )
    );
  }
}
